@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-spinner {
  width: 50px;
  height: 50px;
  border: 6px solid #f3f3f3; /* Light grey */
  border-top: 6px solid #61ce70; /* Black */
  border-radius: 50%;
  animation: spinner 1.5s linear infinite;
}
